/**
 * @param {string} filePath
 */
export default filePath =>
  new Promise((resolve, reject) =>
    fetch(filePath)
      .then(res => res.blob())
      .then(blob => {
        const { type } = blob;
        const reader = new FileReader();
        reader.onload = event => {
          var res = event.target.result;
          const data = res.split(",")[1];
          resolve({ type, data });
        };
        reader.readAsDataURL(blob);
      })
      .catch(reject)
  );
