import React, { useEffect } from "react";
import Button from "./Button";
import styles from "./App.module.css";
import getBase64 from "./getBase64";
import QRCode from "qrcode";

import "./index.css";

import contacts from "./contacts";
import kebabToCamel from "./kebabToCamel";

const getContact = pathname => {
  const kebab = pathname.slice(1);
  const camel = kebabToCamel(kebab);
  switch (camel) {
    case "gisellaAguirre":
      return camel;
    default:
      return undefined;
  }
};

const App = () => {
  useEffect(() => {
    import("particlesjs").then(module => {
      const Particles = module.default;
      Particles.init({
        selector: ".background",
        color: "white"
      });
    });
    import("vcards-js");
  }, []);

  const currentContact = "gisellaAguirre";
  const contact = contacts[currentContact];

  const NormalContent = () => {
    useEffect(() => {
      var canvas = document.getElementById("qr");

      QRCode.toCanvas(canvas, window.location.href, error => {
        canvas.style = "height: 30vmin; width: 30vmin;";
      });
    }, []);
    const { vcardData } = contact;
    const onPress = async () => {
      const vCardsJS = await import("vcards-js").then(module => module.default);
      const FileSaver = await import("file-saver").then(
        module => module.default
      );
      const base64Image = await getBase64(contact.photo);
      const vCard = vCardsJS();
      for (const prop in vcardData) {
        if (vcardData.hasOwnProperty(prop)) {
          const element = vcardData[prop];
          vCard[prop] = element;
        }
      }
      vCard.photo.embedFromString(base64Image.data, base64Image.type);
      vCard.logo.embedFromString(base64Image.data, base64Image.type);
      // vCard.birthday = new Date(1985, 0, 1);

      // vCard.note = "Notes on Eric";
      const content = vCard.getFormattedString();
      const fileName = `${vcardData.firstName}_${vcardData.lastName}`;
      const type = "text/vcard";
      const blob = new Blob([content], { type });
      FileSaver.saveAs(blob, fileName);
    };
    return (
      <>
        <canvas className="background"></canvas>
        <div className={styles.card}>
          <div className={styles.item}>
            <img src={contact.photo} className={styles.AppLogo} alt="logo" />
            <h2>
              {vcardData.firstName} {vcardData.lastName}
            </h2>
            <h4>{vcardData.organization}</h4>
            <h4>{vcardData.title}</h4>
          </div>
          <div className={styles.item}>
            <div className={styles.right}>
              <canvas id="qr" />

              <div style={{ marginBottom: 20 }}>
                <p className={styles.phone}>{vcardData.workPhone}</p>
                <p className={styles.email}>{vcardData.workEmail}</p>
              </div>
            </div>
          </div>
        </div>
        <Button onPress={onPress} text={"Descargar contacto"} />
      </>
    );
  };

  return (
    <div className={styles.App}>
      <header className={styles.AppHeader}>
        <NormalContent />
      </header>
    </div>
  );
};

export default App;
